<template>
	<div class="recommend">
		<scroll class="recommend-content" ref="scroll">
			<div>
				<div v-if="recommends.length" class="slider-wrapper">
						<slider>
							<div v-for="item in recommends">
								<a :href="item.alt" :title="item.original_title">
									<img :src="item.images.large" :alt="item.original_title">
								</a>
							</div>
						</slider>
				</div>
				<div class="recommend-list">
					<h1 class="list-title">热门歌单推荐</h1>
					<ul>
						<li v-for="item in discList" class="item">
							<div class="icon">
								<img :src="item.images.medium" width="60" height="60" alt="">
							</div>
							<div class="text">
								<h2 class="name" v-html="item.title"></h2>
								<p class="desc">
									<span v-for="itemType in item.genres" v-html="itemType"></span>
								</p>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</scroll>
	</div>
</template>

<script type="ecmascript-6">
	import Slider from 'base/slider/slider'
	import Scroll from 'base/scroll/scroll'
	import {getRecommend,getDiscList} from 'api/recommend'
	export default{
		data(){
			return {
				recommends:[],
				discList: [],
			}
		},
		watch: {
			recommends (val) {
				setTimeout(() => {this.$refs.scroll.refresh()}, 20);
			},
			discList (val) {
				setTimeout(() => {this.$refs.scroll.refresh()}, 20);
			}
		},
		created(){
			// setTimeout(() => {this._getRecommend()}, 1000);
			setTimeout(() => {this._getDiscList()}, 1000);
			this._getRecommend()
			// this._getDiscList()
		},
		methods: {
			_getRecommend(){
				getRecommend().then((res)=>{
					this.recommends = res.subjects.slice(0,3)
				})
			},
			_getDiscList(){
				getDiscList().then((res)=>{
					this.discList = res.subjects
				})
			}
		},
		components: {
			Slider,
			Scroll
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .recommend
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .slider-wrapper
        position: relative
        width: 100%
        overflow: hidden
      .recommend-list
        .list-title
          height: 65px
          line-height: 65px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
        .item
          display: flex
          box-sizing: border-box
          align-items: center
          padding: 0 20px 20px 20px
          .icon
            flex: 0 0 60px
            width: 60px
            padding-right: 20px
          .text
            display: flex
            flex-direction: column
            justify-content: center
            flex: 1
            line-height: 20px
            overflow: hidden
            font-size: $font-size-medium
            .name
              margin-bottom: 10px
              color: $color-text
            .desc
              color: $color-text-d
              span+span
              	margin-left: 10px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>


