import jsonp from 'common/js/jsonp'

export function getRecommend () {
  const url = 'https://api.douban.com/v2/movie/in_theaters'
  return jsonp(url, '', '')
}

export function getDiscList () {
  const url = 'https://api.douban.com/v2/movie/top250'
  return jsonp(url, '', '')
}



